// api/user.js
import axios from "axios";

export const updateSettings = async (data, type) => {
  try {
    const url =
      type === "password"
        ? "/api/v1/users/updateMyPassword"
        : "/api/v1/users/updateMe";

    // If it's a password update, transform the data to match API expectations
    const requestData =
      type === "password"
        ? {
            currentPassword: data.passwordCurrent,
            password: data.password,
            passwordConfirm: data.passwordConfirm,
          }
        : data;

    const res = await axios({
      method: "PATCH",
      url,
      data: requestData,
    });

    return res.data;
  } catch (error) {
    throw error;
  }
};

export const loadUsers = async (page, limit, sort, filter, search) => {
  try {
    let query = `?page=${page}&limit=${limit}&sort=${sort}`;
    if (filter) query += `&role=${filter}`;
    if (search) query += `&search=${encodeURIComponent(search)}`;

    const res = await axios.get(`/api/v1/users${query}`);
    return res.data;
  } catch (error) {
    throw error;
  }
};
